	module.exports = {"data":{"site":{"siteMetadata":{"title":"aendrew.com v8","author":"Aendrew Rininsland"}},"markdownRemark":{"id":"/Users/andrew.rininsland/Projects/OTHER/aendrew.github.io/src/pages/2011-12-31-hackney-citizen-houseboaters-being-forced-out-olympics-area/index.md absPath of file >>> MarkdownRemark","html":"<p>First article in the <a href=\"http://www.hackneycitizen.co.uk\">Hackney Citizen</a>, <a href=\"http://hackneycitizen.co.uk/2011/12/30/river-lea-houseboaters-forced-out-olympics/\">“Houseboaters being ‘forced out’ from Olympics area.”</a> It was cover of the January 2012 issue.</p>\n<hr>\n<p><em>British Waterways (BW) has been accused of “forcing out” Hackney’s boaters after it announced plans to limit mooring times and impose fines for overstaying in an area close to the Olympic site.</em></p>\n<p><em>The organisation, which looks after waterways including the Regent’s Canal and River Lea, wants those who live on narrowboats to buy visitor passes, some costing in excess of hundreds of pounds per week, to moor in a specially designated zone close to the site.</em></p>\n<p><a href=\"http://hackneycitizen.co.uk/2011/12/30/river-lea-houseboaters-forced-out-olympics/\">(Read full text on the Hackney Citizen website)</a></p>","frontmatter":{"title":"Hackney Citizen: \"Houseboaters being 'forced out' from Olympics area\"","date":"December 31, 2011"}}},"pathContext":{"path":"content/hackney-citizen-houseboaters-being-forced-out-olympics-area"}}

